import React from "react";


export default function LightBox(props) {
  return (
    <div>
    <a href="#img1">
        <i className="fas fa-award course-certification"></i>
    </a>
    <a href="#_" className="lightbox" id="img1">
      <img src={props.image} alt=""/>
    </a>

    </div>
  );
}
