import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import SummaryData from "../data/SummaryData";
import InfoData from "../data/InfoData";
import Presentation from "./Presentation";
import Info from "./Info";

export default function Summary() {
  const language = useContext(LanguageContext)[0];

  function createInfo(InfoData) {
    return (
      <Info
        key={InfoData.id}
        icon={InfoData.icon}
        text={InfoData.text}
        href={InfoData.href}
        clickable={InfoData.clickable}
      />
    );
  }

  return (
    <div className="summary container-fluid" id="summary">
      <div className="row">
        <Presentation />
        <div className="row col-md-6 info-container">
          {InfoData.map(createInfo)}
        </div>
      </div>
      <div className="row">
        <h1 className="summary-data">{SummaryData[language]}</h1>
      </div>
    </div>
  );
}
