import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import TitleData from "../data/TitleData";

import University from "./University";
import Faq from "./Faq";
import texas from "../images/Other/texas.png";
import unipr from "../images/Other/unipr.png";

export default function Education() {
  const language = useContext(LanguageContext)[0];

  return (
    <div className="education-container" id="education">

      <div className="row">
        <div className="title-section">{TitleData[language][3]}</div>
      </div>

      <div className="row">
        <University
          image={texas}
          course="AI & Machine Learning"
          date="Date: Jun 2019 - Jul 2020"
          type="Post Graduate Program"
        />
        <University
          image={texas}
          course="Cloud Computing"
          date="Date: Jun 2020 - Jul 2014"
          type="Post Graduate Program"
        />
        <University
          image={texas}
          course="Mastering Big Data"
          date="Date: Dec 2020 - Feb 2021"
          type="Post Graduate Program"
        />
        <University
          image={unipr}
          course="Mechanical Engineering"
          date="Date: Sep 2010 - Dec 2014"
          type="Bachelor Degree"
        />
      </div>





    </div>
  );
}
