import React from "react";

export default function University(props) {

  return (
    <div className="col-md-3">
      <img src={props.image} alt="uni-img" className="uni-img" />
      <div className="uni-course">{props.course}</div>
      <div className="row">
        <div className="row skill">
          <div className="tooltips tooltips-education">
            <i className="fas fa-info-circle"></i>
            <span className="tooltiptext">{props.date}</span>
          </div>
          <div className="uni-type">{props.type}</div>
        </div>
      </div>
      <div className="">
        <a class="btn btn-outline-dark" data-toggle="collapse" href="#BD" role="button" aria-expanded="false" aria-controls="BD">Program</a>
      </div>
    </div>
  );
}
